<template>
	<view class='wrapper padding'>
		<view class="form_wrapper">
			<view class="qx-from-group">
				<view class="title">工号<text class='text-red'>*</text></view>
				<input placeholder="请输入员工工号" name="input" v-model="userInfo.code"></input>
			</view>
			<view class="qx-from-group no-border">
				<view class="title">密码<text class='text-red'>*</text></view>
				<input placeholder="请输入初始密码" name="input" type="password" v-model="userInfo.password"></input>
			</view>
		</view>
		<view class="register_btn">
			<button class="cu-btn block lg" @click="register" :disabled="btnDisabled">
				<text v-if="btnDisabled" class="cuIcon-loading2 cuIconfont-spin"></text> 
				注册
			</button>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				btnDisabled:false,
				userInfo:{
					authId:null,//用户授权后生成的记录id	Integer	是	
					type:null,//人员所属类型	Integer	是	1-管理员  2-员工  3-用户
					mobile:null,//	手机号	String	是	
					code:null,//员工或管理员的工号	String是
					password:null//初始密码	String	是
				}
			};
		},
		methods:{
			register(){
				if(!this.userInfo.code){ uni.showToast({title: '请输入员工工号',icon:'none'});return}
				if(!this.userInfo.password){ uni.showToast({title: '请输入初始密码',icon:'none'});return}
				this.btnDisabled=true
				this.request("/user/regsterEmployee",this.userInfo).then((res)=>{
					if(res.resultCode==1006){
						let _token={
							value:res.data.ticket,
							time:new Date().getTime(),
							userType:this.userInfo.type
						}
						uni.setStorageSync('token', JSON.stringify(_token));
						uni.showToast({title:res.resultMsg?res.resultMsg:'操作成功',icon:'none',success:()=>{
							setTimeout(()=>{
								uni.switchTab({
									url:"/pages/personal/info"
								})
							},500)
						}})
					}else{
						this.btnDisabled=false
						uni.showToast({title:res.resultMsg?res.resultMsg:'网络异常，请稍后重试',icon:'none'})
					}
				}).catch((err)=>{
					this.btnLoading=false
					console.log(err)
				})
			},
		},
		onLoad(param){
			this.userInfo.authId=param.authId,
			this.userInfo.type=param.userType
			this.userInfo.mobile=param.mobile
		}
	}
</script>

